.form {
  display: flex;
  flex-direction: column;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(148, 142, 142);
  padding: 30px;
  margin-bottom: 30px;
}

.formLabel {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 18px;
  font-weight: 700;
}

.formInput {
  display: flex;
  width: 300px;
  height: 30px;
  margin-top: 10px;
}

.buttonSubmit {
  width: 150px;
  height: 30px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 700;
  background: rgb(165, 165, 248);
}
